import React from 'react';
import ImageFilter from 'react-image-filter';
import './css/Gallery.css';

import GalleryItems from '../data/gallery-items.json';

class Gallery extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      currSelect: 0
    }
  }
  render() {  
    let getGalleryItem = function(index, _this) {
      let gal = GalleryItems[index];
      let currBgColor = (_this.state.currSelect === index) ?
        "#CCCCCC" : "#DDDDDD";
        
      return (
        <div 
          className="gallery-item" 
          key={index}
          onClick={()=>{
            _this.setState({
              currSelect: index
            });
          }}
          style={{backgroundColor: currBgColor}}
        >
          <img src={"/img/gallery/" + gal.images} style={{filter: "grayscale(1)"}}/>
          <br />
          {gal.name}
        </div>
      );
    }
    let getGalleryItems = function(_this) {
      let galItems = [];
      for (let i = 0 ; i < GalleryItems.length; i++) {
        galItems.push(getGalleryItem(i, _this));
      }
      return galItems;
    }
    let getGalleryDesc = function(index) {
      let gal = GalleryItems[index];
      let galDesc = [<h1 key={-1}>{gal.name}</h1>];
      for (let i = 0; i < gal.desc.length; i++) {
        galDesc.push(<p key={i}>{gal.desc[i]}</p>);
      }
      galDesc.push(<br />);
      galDesc.push(<a href={gal.seemore} style={{color:"#666666"}}>See More</a>);
      return galDesc;
    }

    return(
      <div id="grid-gallery">
        <div id="area-header"/>
        <div id="area-list">
          {getGalleryItems(this)}
        </div>
        <div id="area-desc">
          {getGalleryDesc(this.state.currSelect)}
          {/* {this.state.currSelect} */}
        </div>
      </div>
    );
  }
};
export default Gallery; 


