import $ from 'jquery';
import React from 'react';
import { Link } from 'react-router-dom';

import App from '../App';
import './css/NavigationBar.css';


//jQuery animations
$(document).ready(function(){
    $('.menu-btn').click(function(){
        $('.myNav').slideToggle();
    });

    $( window ).resize(function() {
        if(parseInt($('.myNav').css('width'),10) > 600){
            $('.myNav').css('display', 'block');
        }
        else{
            $('.myNav').css('display', 'none');
        }
    });
});

class NavigationBar extends React.Component{
    constructor(props){
        super(props);
    }
    render(){
        var navTextColor = this.props.navText;
        return(
            <div>
                <div className='menu-btn'><img src="/img/list.png"/></div>
                <div className='myNav' style={{backgroundColor:this.props.navBG}}>
                <ul>
                    <li><a href="/" style={{color:navTextColor}}>bufan</a></li>
                    <li><a href="/gallery" style={{color:navTextColor}}>gallery</a></li>
                    <li><a href="/blog" style={{color:navTextColor}}>blog</a></li>
                    <li><a href="/contact" style={{color:navTextColor}}>contact</a></li>
                </ul>
                </div>
            </div>

        );
    }
}
export default NavigationBar;


