import React from 'react';
import ReactInterval from 'react-interval';
import windowSize from 'react-window-size';

import './css/Home.css';

import quoteList from '../data/quotes.json';

class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state={
			quoteNum:Math.floor(Math.random()*quoteList.length)
    }
    this.randoNumGen = this.randoNumGen.bind(this);
  }
  randoNumGen(){
		var newRandNum = Math.floor(Math.random()*quoteList.length);
		while(this.state.quoteNum === newRandNum){
			//console.log("\t"+newRandNum+" is equal to the quoteNum: "+this.state.quoteNum);
			newRandNum = Math.floor(Math.random()*quoteList.length);
		}
		//console.log("we have picked "+newRandNum);
		this.setState({quoteNum:newRandNum});
		//console.log(this.state);
	}
  render() {
    return(
      <div id="grid-home">
        <ReactInterval timeout={5000} enabled={true}
			  callback={this.randoNumGen} />
        <div id="area-name" className="my-name">
          Bufan <b>Wang</b>
        </div>
        <div id="area-face" className="profilePic">
          <img src="/img/bfwang.png" alt="my face" />
        </div>
        <div id="area-quotes">
          <p>{quoteList[this.state.quoteNum].quote}</p>
			    <p>{'~' + quoteList[this.state.quoteNum].quoted}</p>
        </div>
        <div id="area-intro">
          <h1>Hello There!</h1>
          <p>I am a second year Computer Engineering student attending the University of Waterloo, in Waterloo, Ontario, Canada.</p>
          <p>This is my website.</p>
          <p>{(this.props.windowWidth > 767) ? "⬅" : "⬆"} That is my face.</p>
          <p>I hope to use this as a platform to show off what I have done, and talk about what's been going on.</p>
          <p>Enjoy!</p>
        </div>
      </div>
    )
  }
};
export default windowSize(Home); 


