import React from 'react';

import Routes from './Routes';
import NavigationBar from './comp/NavigationBar';

class App extends React.Component {
  render() {
    return(
      <div>
        <Routes />
        <NavigationBar />
        <div style={{
          position:'fixed',
          height:'120vh',
          width:'120vw',
          background:'pink'
        }} />
      </div>
    )
  }
};
export default App; 


