import React from 'react';
import ReactSVG from 'react-svg'

import './css/Contact.css';

class Contact extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      currSelectIcon: "email"
    }
  }
  render() { 
    let _getContactIcon = function(iconKey, _this) {
      return (
        <div
          id={"area-"+iconKey}
          key={iconKey}
          onClick={()=>{
            _this.setState({
              currSelectIcon: iconKey
            });
          }}
        >
          <ReactSVG
            path={"/img/icons/"+iconKey+".svg"}
            className="contact-icons"
            style={{fill:(
              _this.state.currSelectIcon===iconKey)?
                "#666666":"#333333"
            }}
          />
        </div>
      );
    }
    let _getContactLink = function(i) {
      switch (i){
        case "phone":
          return <a href="sms://+16137169070" target="_blank">
              1-613-716-9070
            </a>;
        case "email":
          return <a href="mailto:bfwang@edu.uwaterloo.ca" target="_blank">
              bfwang@edu.uwaterloo.ca
            </a>;
        case "linkedin":
          return <a href="http://www.linkedin.com/in/bu-fan-wang" target="_blank">
              www.linkedin.com/in/bu-fan-wang
            </a>;
        case "github":
          return <a href="https://github.com/boowangoo" target="_blank">
              github.com/boowangoo
            </a>;

      }
    }
    return(
      <div id="grid-contact">
        <div id="area-title">
          <h1>Contact me using:</h1>
        </div>
        {_getContactIcon("phone", this)}
        {_getContactIcon("email", this)}
        {_getContactIcon("linkedin", this)}
        {_getContactIcon("github", this)}
        <div id="area-result">
          {_getContactLink(this.state.currSelectIcon)}
        </div>
      </div>
    );
  }
};
export default Contact;


